@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
/*! Variables: */
:root {
	--main-background-red: rgb(183, 0, 0);
	--hinge-red: rgb(171, 0, 0);
	--secondary-hinge-red: rgb(162, 5, 5);
	--actual-background-color: white;
	--top-section-red: rgb(222, 0, 0);
	--d-pad-background: rgb(113, 113, 113);

	--outermost-rounding: 1em;
	--small-radius: 5px;

	--grid-button-dimension: 40px;

	--info-div-margin: 10px;
}

