/* creating the layout for both large halves of the pokedex to sit in */
.entire-pokedex {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: flex-end;
}

/* the div containing left half of pokedex,
 including the hinge + pic screen + number */
.left-side {
	width: 325px;
	height: 500px;
	background-color: var(--main-background-red);
	position: relative;
	/* creating rounded edges */
	border-top-left-radius: var(--outermost-rounding);
	border-top-right-radius: var(--small-radius);
	border-bottom-left-radius: var(--outermost-rounding);
}

/* div containing right half of pokedex, with info screen */
.right-side {
	position: relative;
	width: 300px;
	height: 400px;
	background-color: var(--main-background-red);

	/* creating rounded edges */
	border-top-right-radius: var(--outermost-rounding);
	border-bottom-right-radius: var(--outermost-rounding);
}

/* the hinge of the pokedex */
.poke-hinge {
	/* sizing and positioning */
	width: 25px;
	height: 500px;
	position: absolute;
	left: 300px;
	/* make sure the hinge outline is inside the box */
	box-sizing: border-box;
	/* color and borders */
	background-color: var(--hinge-red);
	border-top-right-radius: var(--small-radius);
	border: 1px solid rgb(108, 55, 55);
}
.poke-hinge > .inner-hinge-top {
	position: absolute;
	box-sizing: border-box;
	width: 25px;
	height: 25px;
	background-color: var(--secondary-hinge-red);

	border: 1px solid rgb(108, 55, 55);

	left: -1px;
	top: 100px;
}
.poke-hinge > .inner-hinge-bottom {
	position: absolute;
	box-sizing: border-box;
	width: 25px;
	height: 25px;
	background-color: var(--secondary-hinge-red);

	border: 1px solid rgb(108, 55, 55);

	left: -1px;
	top: 375px;
}

/* Upper right slanted thing */
.upper-right-triangle-cover {
	position: absolute;
	top: -50px;
	width: 125px;
	height: 50px;
	background-color: var(--main-background-red);
}
.upper-right-triangle {
	box-sizing: content-box;
	position: absolute;
	top: -100px;
	left: 75px;

	border-style: solid;
	border-color: transparent transparent var(--main-background-red) transparent;
	border-width: 50px;
}

/* Big buttons on bottom of right side */
.big-buttons {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	width: 300px;

	position: absolute;
	top: 330px;
}

.big-button {
	width: 100px;
	height: 50px;
	border-radius: 5px;

	background-color: rgb(51, 51, 51);
}

/* The main gray div for the screen component */
.pic-screen {
	position: absolute;
	background-color: rgb(214, 214, 214);
	top: 120px;
	left: 15px;
	width: 270px;
	height: 175px;

	border-radius: var(--outermost-rounding);
}
/* two white blocks cover the gray: make the "actual screen" */
.pic-screen-white-1 {
	background-color: white;
	position: absolute;
	top: 10px;
	left: 10px;
	width: 250px;
	height: 130px;
	border-radius: var(--small-radius);
}
.pic-screen-white-2 {
	background-color: white;
	position: absolute;
	top: 10px;
	left: 100px;
	width: 160px;
	height: 160px;
	border-radius: var(--small-radius);
}
/* the small yellow button */
.pic-screen-yellow {
	width: 12px;
	height: 12px;
	background-color: yellow;
	position: absolute;

	top: 150px;
	left: 20px;

	border-radius: 99999px;
}
/* The button for reversing the images */
.pic-screen-button {
	position: absolute;
	top: 148px;
	left: 38px;
	font-size: 12px;
	width: 12px;
	padding: 0;
	background-color: transparent;
	border: none;

	cursor: pointer;
}

/* creating the necessary animations for the pokemon sprites */
@keyframes bounce {
	from {
		top: -9px;
	}
	to {
		top: -6px;
	}
}
/* sizing/styling the actual image of the pokemon */
.pokemon-img {
	display: block;
	position: absolute;
	left: 45px;
	top: -10px;
	width: 150px;
	z-index: 1;

	/* using the created key frames to animate the pokemon */
	animation-name: bounce;
	animation-direction: alternate;
	animation-duration: 2000ms;
	animation-iteration-count: infinite;
}

/* The small decorative "speaker" */
.four-vents {
	position: absolute;
	top: 147px;
	left: 65px;
	height: 20px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
.vent {
	width: 30px;
	height: 2px;
	background-color: rgb(166, 166, 166);
	border-radius: 2000px;
}

/* The upper left section/triangle thing */
.upper-section {
	background-color: var(--top-section-red);
	position: absolute;
	width: 300px;
	height: 100px;
	border-top-left-radius: var(--outermost-rounding);
}

/* Upper left details: */
.upper-section-circle {
	border-radius: 900px;
	border: 4px solid white;
	background-color: rgb(132, 207, 248);
	position: absolute;
	width: 50px;
	height: 50px;
	top: 25px;
	left: 25px;
}

.upper-section-three-dots {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 55px;
	top: 15px;
	left: 100px;
	position: absolute;
}
.dot-1,
.dot-2,
.dot-3 {
	width: 12px;
	height: 12px;
	background-color: white;
	border-radius: 90000px;
}
.dot-1 {
	background-color: green;
}
.dot-2 {
	background-color: yellow;
}
.dot-3 {
	background-color: red;
}

/* Upper left triangle etc. */

.upper-left-triangle-cover {
	position: absolute;
	top: 50px;
	left: 175px;
	width: 125px;
	height: 50px;
	background-color: var(--main-background-red);
}
.upper-left-triangle {
	box-sizing: content-box;
	position: absolute;
	left: 125px;

	border-style: solid;
	border-color: transparent transparent var(--main-background-red) transparent;
	border-width: 50px;
}

#choose-pokemon-number {
	background-color: rgb(0, 118, 0);
	width: 70px;
	height: 70px;

	padding-left: 10px;

	position: absolute;
	top: 330px;
	left: 10px;

	border: none;
	border-radius: var(--small-radius);

	font-family: "Share Tech Mono", monospace;
	font-size: 40px;
}

.num-buttons {
	position: absolute;
	top: 330px;
	left: 110px;

	display: grid;
	grid-template-columns: repeat(4, var(--grid-button-dimension));
	grid-template-rows: var(--grid-button-dimension) var(--grid-button-dimension);
	grid-gap: 3px;
	gap: 3px;
}

.num-button {
	background-color: rgb(103, 103, 224);
	border: none;
	border-radius: var(--small-radius);

	transition-timing-function: ease-in-out;
	transition-duration: 300ms;
}

.num-button:hover {
	background-color: rgb(255, 93, 93);
}
.num-button:active {
	background-color: red;
}

.side-arrows-container {
	position: absolute;
	top: 440px;
	left: 20px;
	width: 260px;

	display: flex;
	flex-direction: row;
}

.side-arrows-container > button {
	width: 50%;
	height: 35px;
	font-size: 25px;

	border-width: 0 0 2px 0;

	transition-timing-function: ease-in-out;
	transition-duration: 100ms;
}
.side-arrows-container > button:active {
	border-width: 0px;
	margin-top: 1px;
}

.side-arrows-container button:first-child {
	/* background-color: blue; */
	border-top-left-radius: var(--small-radius);
	border-bottom-left-radius: var(--small-radius);

	border-top-right-radius: 0;
	border-bottom-right-radius: 0;

	border-right-width: 1px;
}

.side-arrows-container button:last-child {
	border-top-left-radius: 0;
	border-bottom-left-radius: 0;

	border-top-right-radius: var(--small-radius);
	border-bottom-right-radius: var(--small-radius);

	border-left-width: 1px;
}

.info-screen {
	box-sizing: border-box;
	position: absolute;
	left: 20px;
	top: 20px;

	width: 260px;
	height: 220px;
	background-color: white;

	border-radius: var(--outermost-rounding);
	padding: 20px;
	overflow: scroll;

	font-family: "Roboto Mono", monospace;
}
.info-container {
	position: absolute;
}

#pokemon-name {
	text-transform: capitalize;
}

.info-division {
	width: 100%;
	height: 5px;
	background-color: black;
	border-radius: 1000px;
	margin: var(--info-div-margin) 0;
}

.info-screen th,
.info-screen td {
	text-align: left;
}

.info-screen .statistics-th {
	border-bottom: 2px solid black;
}

/* Container for d-pad and zoom buttons */
.zoom-buttons {
	position: absolute;
	top: 250px;
	width: 300px;

	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
}
/* zoom buttons */
.zoom {
	height: 26px;
	display: flex;
	flex-direction: row;
}
.zoom-out,
.zoom-in {
	box-sizing: border-box;

	width: 45px;
	height: 25px;

	border-color: black;
	border-width: 0 0 2px;
}

.zoom-out:active,
.zoom-in:active {
	border-bottom: none;
	margin-top: 0.5px;
}

.zoom-in {
	border-right: 1px solid black;
	border-radius: var(--small-radius) 0 0 var(--small-radius);
}
.zoom-out {
	border-left: 1px solid black;
	border-radius: 0 var(--small-radius) var(--small-radius) 0;
}

/* ---------------------------------------------------- */

/* creating the scrolling section/d-pad */
/* the container div for the 9 divs/buttons */
.scroll {
	display: grid;
	grid-template-rows: repeat(3, 25px);
	grid-template-columns: repeat(3, 25px);
	/* background-color: var(--d-pad-background); */
	background-color: var(--main-background-red);
}
/* styling the actual buttons in this section */
.scroll-btn {
	width: 100%;
	top: 0;
	height: 25px;
	background-color: var(--d-pad-background);

	border: none;
	border-radius: var(--small-radius);

	transition-duration: 200ms;
	transition-timing-function: ease-in-out;
}
.scroll-btn:hover {
	-webkit-filter: brightness(95%);
	        filter: brightness(95%);
}

.scroll .up,
.scroll .left,
.scroll .right,
.scroll .down {
	background-color: var(--main-background-red);
}

.scroll .d-pad.center {
	background-color: var(--d-pad-background);
}

.scroll .upper-left-corner,
.scroll .upper-right-corner,
.scroll .lower-left-corner,
.scroll .lower-right-corner {
	background-color: var(--main-background-red);
}

/* Each of the divs has a slightly different style... */
.scroll .upper-left-corner {
	border-bottom-right-radius: var(--small-radius);
}
.scroll .upper-right-corner {
	border-bottom-left-radius: var(--small-radius);
}
.scroll .lower-left-corner {
	border-top-right-radius: var(--small-radius);
}
.scroll .lower-right-corner {
	border-top-left-radius: var(--small-radius);
}
/* For the buttons: */
.scroll .up-btn {
	border-radius: var(--small-radius) var(--small-radius) 0 0;
}
.scroll .left-btn {
	border-radius: var(--small-radius) 0 0 var(--small-radius);
}
.scroll .down-btn {
	border-radius: 0 0 var(--small-radius) var(--small-radius);
}
.scroll .right-btn {
	border-radius: 0 var(--small-radius) var(--small-radius) 0;
}

.App {
	display: flex;
	justify-content: center;
	padding-top: 100px;
	padding-bottom: 100px;
}

